	import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue";
	import uniNavBar from "@/components/uni-nav-bar/uni-nav-bar.vue"

	export default {
		components: {
            uniNavBar,
			uniLoadMore
		},
		data() {
			return {
                height:"",
				style4: "",
				desc: "", //退货描述
				num: "", //取消订单下标
				order: "", //取消订订单编号
				show_menu: false,

				ishow: false,

				selceted: "",
				headerPosition: "fixed",
				headerTop: "0px",
				typeText: {
					unpaid: '等待付款',
					back: '等待商家发货',
					unreceived: '商家已发货',
					received: '等待用户评价',
					completed: '交易已完成',
					refunds: '商品退货处理中',
					cancelled: '订单已取消'
				},
				orderType: ['全部', '待付款', '待发货', '待收货', '已完成'],
				//订单列表 演示数据
				orderList: [],
				list: [],
				tabbarIndex: 0,
				array: [{
						id: 0,
						name: "我不想买了"
					}, {
						id: 1,
						name: "信息填写错误"
					}, {
						id: 2,
						name: "重新拍"
					},
					{
						id: 3,
						name: "卖家缺货"
					}, {
						id: 4,
						name: "其他原因"
					}
				],
				current_page: 1,
				last_page: "",
				status: "more",
			};
		},
		onReachBottom() {
			this.current_page++
			if (this.current_page > this.last_page) {
				return;
			} else {
				this.requestOrder(this.current_page);
			}
		},
		onLoad(option) {

			//option为object类型，会序列化上个页面传递的参数
			//console.log("option: " + JSON.stringify(option));
			let tbIndex = parseInt(option.tbIndex) + 1;

			this.tabbarIndex = tbIndex;
		//	console.log("onload",this.tabbarIndex)
			//兼容H5下排序栏位置









		},
		onShow() {
			this.current_page = 1;
			this.orderList = []
			this.requestOrder(this.current_page)
			
		},

		onPageScroll(e) {
			return;
			//兼容iOS端下拉时顶部漂移
			this.headerPosition = e.scrollTop >= 0 ? "fixed" : "absolute";
		},
		methods: {
			//返回个人中心
			goBack(){
				
				uni.switchTab({

					url:"/pages/tabBar/user"
				})
			},
			//请求订单
			requestOrder(page) {
				this.request({
					url: this.config.url + "member/order",
					method: "post",
					data: {
						token: this.token,
						type: this.tabbarIndex,
						page: page
					},
					success: (res) => {
						//console.log("moun",res)
						if (res.data.code == 1) {
							this.last_page = res.data.data.last_page;
							this.orderList = this.orderList.concat(res.data.data.data)
							this.status = "more";
							if (this.current_page >= this.last_page) {
								this.status = "noMore";
							}
							this.orderList.forEach((item, index) => {
								let num = 0;
								item.order_list.forEach(e => {
									num += e.number
								})
								item.sum = num;

							})
						}
					}
				})
			},
			//顶部栏切换
			// showType(tbIndex) {
			// 	this.tabbarIndex = tbIndex;
			// 	this.list = this.orderList[tbIndex];
			// },
			//选中退货原因
			showcityfour(id, name) {
				this.style4 = id;
				this.desc = name;
			},
			ToDetail(id) {
				uni.navigateTo({
					url: "../../detail/detail?id=" + id
				})
			},
			//切换导航栏
			showType(tbIndex) {
			//	console.log(tbIndex)
				this.request({
					url: this.config.url + "member/order",
					method: "post",
					data: {
						token: this.token,
						type: tbIndex
					},
					success: (res) => {
						console.log("moun", res)
						if (res.data.code == 1) {

							this.orderList = res.data.data.data;
							this.orderList.forEach((item, index) => {
								let num = 0;
								item.order_list.forEach(e => {
									num += e.number
								})
								item.sum = num;

							})
						}
					}
				})
				this.tabbarIndex = tbIndex;
				this.list = this.orderList[tbIndex];
			},
			//去付款
			toPayment(order, index) {
				//调起支付接口
				var _this = this;
				this.request({
					url: this.config.url + "order/pay",
					method: "POST",
					data: {
						token: this.token,
						order_no: order
					},
					success: (res) => {
						console.log(res)
						if (res.data.code == 1) {
							var pay = res.data.data.data
							uni.requestPayment({
								provider: 'wxpay',
								appid: pay.appId,
								timeStamp: pay.timeStamp,
								nonceStr: pay.nonceStr,
								package: pay.package,
								signType: pay.signType,
								paySign: pay.paySign,
								success: function(res) {
									console.log('success:' + JSON.stringify(res));
									_this.orderList.splice(index, 1)
									uni.showToast({
										title: "支付成功",

									})
								},
								fail: function(err) {
									console.log('fail:' + JSON.stringify(err));
									uni.showToast({
										title: "支付失败"
									})
								}
							})
						} else {
							uni.showToast({
								title: res.data.info
							})
						}
					}
				})

			},
			//取消订单
			cancelOrder(order, index) {
				console.log(order, index)

				this.show_menu = true;
				this.num = index;
				this.order = order;

			},
			// 取消
			cancel() {
				this.show_menu = false
			},
			//确认
			sure() {
				if (this.desc) {

					this.show_menu = false
					this.request({
						url: this.config.url + "order/cancle",
						method: "POST",
						data: {
							token: this.token,
							order_no: this.order,
							cancle_desc: this.desc
						},
						success: res => {
							console.log(res)
							this.orderList.splice(this.index, 1)
							if (res.data.code == 1) {
								uni.showToast({
									title: "取消订单成功"
								})
							} else {
								uni.showToast({
									title: "取消订单失败"
								})
							}
						}
					})
				} else {
					uni.showToast({
						title: "请选择退货原因",
						icon: 'none'
					})

				}

				console.log(order, index)
				this.ishow = true;

				// this.request({
				// 	url:this.config.url+"order/cancle",
				// 	method:"POST",
				// 	data:{
				// 		token:this.token,
				// 		order_no:order,
				// 	},
				// 	success:res =>{
				// 		console.log(res)
				// 		this.orderList.splice(index,1)
				// 		if(res.data.code==1){
				// 			uni.showToast({
				// 				title:"取消订单成功"
				// 			})
				// 		}else{
				// 			uni.showToast({
				// 				title:"取消订单失败"
				// 			})
				// 		}
				// 	}
				// })

			},
			//确认收货
			confirm(order, row) {
				this.request({
					url: this.config.url + "order/confirm",
					method: "POST",
					data: {
						token: this.token,
						order_no: order,
					},
					success: res => {
						if (res.data.code == 1) {
							var con = JSON.stringify(row)
							console.log("确认收货", res)
							uni.navigateTo({
								url: "/pages/confirm/confirm?con=" + con
							})
						} else {
							uni.showToast({
								title: res.data.info
							})
						}

					}
				})
			},
			//查看物流
			viewSend(order, row) {
				this.request({
					url: this.config.url + "order/express",
					method: "POST",
					data: {

						order_no: order,
					},
					success: res => {
						console.log(res)
						if (res.data.code == 1) {
							var goods = JSON.stringify(row)
							var info = JSON.stringify(res.data.data)
							uni.navigateTo({
								url: "/pages/viewsend/viewsend?info=" + info + "&goods=" + goods
							})
						} else {
							uni.showToast({
								title: res.data.info
							})
						}
					}
				})
			},
			//申请售后
			service(row) {
				uni.navigateTo({
					url: "/pages/serType/serType"
				})
				uni.setStorage({
					key: "regoods",
					data: row,
					success: () => {
						console.log('保存换货数据成功')
					}
				})
			},
			//删除订单
			deleteOrder(order, index) {
				this.request({
					url: this.config.url + "order/del",
					method: "POST",
					data: {
						token: this.token,
						order_no: order,
					},
					success: res => {
						console.log(res)
						this.orderList.splice(index, 1)
						if (res.data.code == 1) {
							uni.showToast({
								title: "删除订单成功"
							})
						}
					}
				})
			},
			//跳转订单
			evalute(item) {
				console.log("item", item)
				var goods = JSON.stringify(item)
				uni.navigateTo({
					url: "/pages/user/keep/sayFeel/sayFeel?eval=" + goods
				})
			}
		}
	}
